function remove ()
{
    $.ajax({
        'url': `//${window.location.host}/note/${$(this).closest('.notes__item').attr('data-id')}/remove`,
        'method': 'POST',
        success: () => $(this).closest('.notes__item').remove()
    });
}

function edit ()
{
    $.ajax({
        'url': `//${window.location.host}/note/${$(this).closest('.notes__item').attr('data-id')}/edit`,
        'method': 'POST',
        'data': {
            'content': $(this).val()
        },
        success: (data) => {
          
        }
    });
}

$('.notes__close').on('click', remove);
$('.notes__text').on('focusout', edit);
$('.notes__add-btn').click(function (e) {
    e.preventDefault
    $.ajax({
        'url': `//${window.location.host}/note`,
        'method': 'POST',
        success: function (data) {
            let date = new Date(data.created_at);
            let output = document.getElementById('outputNotes');

            output.innerHTML += `<div class="notes__item" data-id="${data.id}">` +
                '<div class="notes__box">' +
                    '<textarea class="notes__text" name="content" placeholder=" ">' +
                    '</textarea>' +
                    '<div class="notes__bottom">' +
                        '<div class="notes__date">'+
                            ': ' +
                            '<span>' +
                                date.getMonth() + '/' + date.getDate() + '/' + date.getFullYear()
                                + date.getHours() + ':' + (date.getMinutes() + 1) +
                            '</span>' +
                        '</div>' +
                    '</div>' +
                '</div>' +
                '<div class="notes__icons">' +
                    '<div class="notes__close"></div>' +
                '</div>' +
            '</div>';

            $('.notes__close').on('click', remove);
            $('.notes__text').on('focusout', edit);
        }
    })
})

$('.notes__color').click(function (e) {
    $.ajax({
        'url': `//${window.location.host}/note/${$(this).closest('.notes__item').attr('data-id')}/edit-color`,
        'method': 'POST',
        success: function (data) {
            let color = $(this);
            color.closest('.notes__item').css('background', color.attr('data-color'));
            $('.notes__color').removeClass('notes__color_selected');
            color.addClass('notes__color_selected');
        }
    })
})
 